<template>
  <div class="is-relative" style="height: 100%">
    <div class="home" style="border-top: 1px solid #dbdbdb">
      <h1 class="gn-title is-border">GnConfEngine</h1>
      <p class="gn-sub-title">
        Genians 제품에 사용되는 설정화면을 렌더링하는 Front-end Config Engine.
      </p>
      <h4 class="gn-title is-size4">모듈 설치</h4>
      <h4>사용하고자 하는 버전의 URL을 사용하여 모듈을 설치한다.</h4>
      <div class="gn-box-no-line">
        <i
          >npm install -S
          https://download.genians.com/tftpboot/GNUI/CURRENT/GNOS/v1.0/GNUI-CT64-C-current/gnConfEngine.tar.gz</i
        >
      </div>
      <ul class="gn-list">
        <li>
          설치된 <u>js 파일</u>과 사용하고자 하는 <u>테마 css 파일</u>을 소스에 import하여 사용한다.
        </li>
        <li>
          * 버전별 모듈 경로는 제품
          다운로드(https://download.geninetworks.com/tftpboot/GNUI/CURRENT/GNOS/v1.0/)에서 확인하여
          설치
        </li>
      </ul>
      <h6 class="gn-title is-size6">Browser</h6>
      <GnUI
        uiType="syntaxinput"
        :options="this.getSyntaxInputOptions('html', '85px')"
        value='<link href="gnui/dist/styles/default.css" rel="stylesheet"/>
<script src="gnui/dist/js/gnui.js"></script><script>window.Gn = gnui</script>
<link href="gnConfEngine/dist/styles/gnConfEngine.css" rel="stylesheet"/>
<script src="gnConfEngine/dist/js/gnConfEngine.js"></script><script>window.GnConfEngine = gnConfEngine</script>'
      ></GnUI>
      <h6 class="gn-title is-size6"></h6>
      <h6 class="gn-title is-size6">ESM</h6>
      <GnUI
        uiType="syntaxinput"
        :options="this.getSyntaxInputOptions('javascript', '85px')"
        value="import Gn from 'gnui'
import 'gnui/dist/styles/default.css'
import GnConfEngine from 'gnConfEngine'
import 'gnConfEngine/dist/styles/gnConfEngine.css'"
      ></GnUI>
      <h6 class="gn-title is-size6"></h6>

      <h4 class="gn-title is-size4">엔진 설정</h4>
      <h4>엔진을 사용하기 위해 초기 설정을 해준다.</h4>
      <GnUI
        uiType="syntaxinput"
        :options="this.getSyntaxInputOptions('javascript', '500px')"
        :value="engineInitDisp"
      ></GnUI>
      <h6 class="gn-title is-size6"></h6>
      <h4>데이터를 출력한다.</h4>
      <GnUI
        uiType="syntaxinput"
        :options="this.getSyntaxInputOptions('javascript', '300px')"
        :value="engineRenderDisp"
      ></GnUI>
    </div>
  </div>
</template>

<script>
  import GnUI from '@/components/core/GnUI'
  import ko from '../locales/ko.json'

  const gnuiMessage = JSON.stringify(ko.gnuiMessage)
    .replace('{', '{\n  ')
    .replaceAll('","', '",\n  "')
  const engineInitDisp = `// 페이지 url
GnConfEngine.setBaseUrl('http://localhost:8080/');
// 지역 설정 (en이 기본값)
GnConfEngine.setDefaultLocal('ko');
// 언어 설정
GnConfEngine.setSupportLang(['en', 'ko']);

// 컴포넌트의 옵션값 설정을 위한 함수들을 앞서 정의한 후 바인딩
// 컴포넌트마다 사용하는 함수들이 다르기 때문에 출력하고자 하는 컴포넌트에서 확인
GnConfEngine.setConfDataProvider({
  getConf,
  getConfList,
  getConfUIOptionList,
  getConfSqlMapOptionList,
  getCodeMapOptionList,
  getSelector,
  getCustom
});

// fileupload 컴포넌트 같이 컴포넌트 내에서 api 통신이 필요한 경우 함수를 앞서 정의한 후 바인딩
GnConfEngine.setUploadFileProvider({
	getFile,
	getFileWithKey,
	postFile,
	putFile,
	deleteFile
});

// 컴포넌트 내에서 사용하는 상수는 locale 기준으로 보여주며, 다른 라벨로 사용하고 싶다면 {key:value}로 정의
GnConfEngine.setMessageData(${gnuiMessage}
);`
  const engineRenderDisp = `// 출력에는 두 가지 방법이 있다. create 와 render 다.
// 출력시킬 conflist 데이터가 있는 경우 render 를 사용하고, conflist의 카테고리 키워드를 아는 경우 create를 사용한다.
GnConfEngine.create(target, category, options);
GnConfEngine.render(target, options);

// target - 선택자, conflist를 출력할 웹 페이지의 HTML 요소
// options - 출력할 옵션를 같이 매개변수로 전달
const options = {
  // CONF_KEY 기준으로 컴포넌트의 defaultValue가 있으면 작성
	defaultValues:{},
  // CONF_KEY 기준으로 컴포넌트 별로 외부에 제공하는 events가 있으면 작성
	events:{},
	change,
  // 응답 데이터의 프로퍼티 키값이 대문자인 경우 'upper' 로 설정, NAC 설정 필요
	columnType: 'upper',
  // confList 데이터
	confList,
  // cleanXSS 옵션 적용 여부
	cleanXSS: true
};`

  export default {
    name: 'Home',
    components: {
      GnUI
    },
    data() {
      return {
        engineInitDisp,
        engineRenderDisp
      }
    },
    methods: {
      getSyntaxInputOptions(type, height) {
        return {
          multiple: true,
          readonly: true,
          type: type,
          height: height
        }
      }
    }
  }
</script>
<style>
  .home {
    padding: 15px 30px;
  }
  .app-container {
    overflow-y: auto;
  }
</style>
